@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.App{text-align:center}:root{--primary-white: #ffffff}body{font-family:"Roboto", sans-serif}.logo{width:2rem}.navbar{height:8vh}.navbar .navbar-nav>li>a{color:#ffffff !important;color:var(--primary-white) !important;border-top:3px solid transparent}.navbar .navbar-nav>li>a.active,.navbar .navbar-nav>li>a:hover{border-top:3px solid;border-color:red}.navbar .navbar-collapse.show{background:#666;padding-left:1rem;z-index:9}.page-content{height:82vh;overflow-y:auto;position:relative}.footer{height:10vh;color:#ffffff;background-color:#343a40;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.laptop-image-wrapper{position:relative}.laptop-image-wrapper .laptop-image{width:100%}.laptop-image-wrapper .typed-content{position:absolute;left:18%;top:10%;width:66%;font-weight:700}.laptop-image-wrapper .typed-content .typed-cursor{display:none}.page-content .container #tsparticles{position:absolute;width:100%;top:0;left:0;height:80vh}.page-content .container .header-wrapper{height:80vh;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.share-buttons-wrapper{position:absolute;left:0;top:30%}.share-buttons-wrapper a{color:white;padding:0.1rem 0.5rem;font-size:2rem;background:#666;border-radius:0 1.5rem 1.5rem 0}.share-buttons-wrapper a:hover{color:#f44242;padding-left:1rem}.share-buttons-wrapper .linkedin-icon a{padding-right:0.7rem}.share-buttons-wrapper .envelope-icon a{font-size:1.8rem;padding-right:0.7rem}@media (min-width: 768px){.navbar{height:10vh}.logo{width:3rem}.page-content{height:85vh}.page-content .container #tsparticles{height:85vh}.page-content .container .header-wrapper{height:85vh}.footer{height:5vh}.laptop-image-wrapper .typed-content{font-size:1.5rem}}

